import firebase from '../../firebase'

const db = firebase.firestore()

const users = {
  namespaced: true,
  state: {
    all: [],
    one: {},
  },
  getters: {
    all: state => state.all,
  },
  actions: {
    getAll({ commit }) {
      db.collection('users').get().then(resopnse => {        
        let users = []
        resopnse.forEach(element => {
          users = [...users, element.data()]
        })
        commit('setAllUsers', users)
      })
    },
    create({ dispatch }, data) {
      return db.collection('users').add(data).then(() => {
        dispatch('getAll')
      })
    }
  },
  mutations: {
    setAllUsers(state, data) {
      state.all = data
    }
  },
}

export default users