<template>
  <div id="app">
    <div class="head">
      <h2 class="title">Chief Detector</h2>
      <toggle v-model="toggle" @change="onChangeToggle" />
    </div>
    <div class="body">
      <history :data="items" @remove="onClickRemove" />
    </div>
  </div>
</template>

<script>
import Toggle from './components/Toggle.vue'
import History from './components/History.vue'
import { mapActions, mapGetters } from 'vuex'

export default { 
  name: 'app',
  components: {
    Toggle,
    History,
  },
  data() {
    return {
      OneSignal: window.OneSignal || [],
      toggle: false,
    }
  },
  mounted() {
    this.initOneSignal()
    this.getAllUsers()
    this.getAllHistory()
  },  
  computed: {
    ...mapGetters({
      users: 'users/all',
      items: 'history/all',
    }),
  },
  methods: {
    ...mapActions({
      getAllUsers: 'users/getAll',
      getAllHistory: 'history/getAll',
      createUser: 'users/create',
    }),
    async onChangeToggle(value) {
      const support = this.OneSignal.isPushNotificationsSupported()
      const enable = await this.OneSignal.isPushNotificationsEnabled(async isEnabled => isEnabled)         
      if (value) {                          
        if (support && ! enable) {
          this.OneSignal.showNativePrompt()
          this.OneSignal.setSubscription(value)
        }
        return
      }

      this.OneSignal.setSubscription(value)
    },
    initOneSignal() {
      if ( ! this.OneSignal.initialized) {
        this.OneSignal.init({
          appId: "abae5cc9-ffd9-45bc-b7b9-a74aec78ec64",
          notifyButton: {
            enable: false,
          },
        })
      }

      this.OneSignal.isPushNotificationsEnabled(enable => {
        this.OneSignal.getSubscription(subscribe => {
          this.OneSignal.getUserId((user_id) => {
            if ( ! this.users.find(u => u.browser_id == user_id)) {
              this.createUser({browser_id: user_id})
            }
          })
          this.toggle = enable && subscribe
        })
      })
    },
    onClickRemove(index) {
      alert(index)
    },
  },
}
</script>

