import firebase from '../../firebase'

const db = firebase.firestore()

const history = {
  namespaced: true,
  state: {
    all: [],
  },
  getters: {
    all: state => state.all,
  },
  actions: {
    getAll({ commit }) {
      db.collection('history').get().then(resopnse => {        
        let history = []
        resopnse.forEach(element => {
          history = [...history, element.data()]
        })
        commit('setAll', history)
      })
    },
  },
  mutations: {
    setAll(state, data) {
      state.all = data
    },
  },
}

export default history